import Vue from 'vue'
import Router from 'vue-router'
import MainScene from '@/components/MainScene'
import GameHall from '@/components/GameHall'
import Table from '@/components/Table'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'MainScene',
    component: MainScene
  }, {
    path: '/game/:type',
    name: 'GameHall',
    component: GameHall
  }, {
    path: '/table/:id',
    name: 'Table',
    component: Table
  }]
})



