<template>
  <div class="main-scene">
    <div class="bg-portal"></div>
    <div class="content">
      <img class="main-logo" src="../../static/img/shaizi.png" alt="">
      <a class="btn-type one-eos" href="#/game/1"></a>
  
      <a class="btn-type ten-eos" href="#/game/10"></a>

      <a class="btn-type hundred-eos" href="#/game/100"></a>
      
    </div>
  </div>
</template>

<script>
export default {
  name: "MainScene",
  data() {
    return {
      msg: "Welcome to Your Vue.js App"
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main-scene {
  position: relative;
  width: 100%;
  max-width: 1280px;
  min-height: 500px;
  text-align: center;
  margin: 60px auto;
}

.bg-portal {
  background-image: url("../../static/img/bg.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: fixed;
  min-height: 600px;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.content {
  position: absolute;
  z-index: 99;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  vertical-align: middle;
}

.btn-type {
  display: block;
  margin: 1% auto;
  width: 90%;
  max-width: 500px;
  height: 25%;
  background-size: 100% 100%;
  background-repeat: no-repeat;
}

.one-eos {
  background-image: url("../../static/img/btn-1eos.png");
}

.ten-eos {
  background-image: url("../../static/img/btn-10eos.png");
}
.main-logo {
    width: 60%;
    max-width: 300px;
}
.hundred-eos {
  background-image: url("../../static/img/btn-100eos.png");
}

@media screen {
  .btn-type {
    
  }
}
</style>



