<template>
  <div class="game-hall">
    <div class="bg-table"></div>
    <div class="the-table"></div>
    <div class="content">
        <div class="opt-bar">
            <div @click="back" class="back"></div>
            
            <div class="float-right">
                <div v-if="currentAccount" class="user-info">
                    <span class="user-balance">{{balance}}</span>
                    <span class="user-name">{{currentAccount}}</span>
                    <a href="javascript:;" class="btn-common exit" @click="logout">退出</a>
                </div>
                
                <a href="javascript:;" class="btn-common" v-else @click="login">登录</a>
            </div>
            
        </div>
        <div class="table-title">
            <span v-if="chip"> {{tableId}}号桌:  {{ parseFloat(chip)}}{{symbol}} / 位 </span>
        </div>
        <div :class="['user-list', 'user-list-' + (tableUserCount - 1)]">
            <div class="user" v-for="(user, index) in userList">
                <span :class="['seat-' + index, 'user-seat']" v-if="user">
                        <img class="avatar" src="../../static/img/avatar.png" alt="">
                        {{user}}</span> 
                <span v-else><span @click="join()" :class="['seat-' + index, 'empty-seat']" >+</span>
                <span class="waiting">等待加入</span></span>
            </div>
        </div>

        <div v-if="runDice" class="run-dice">
            <img :src="'../../static/img/dice' + diceNum + '.png'" alt="">
        </div>

        <div class="my-seat">
            <div v-if="alreadyIn && currentAccount">
                <span class="user-seat"><img class="avatar" src="../../static/img/avatar.png" alt="">
                        {{currentAccount}}</span> 
            </div>
            <div v-else>
                <span @click="join()" class="empty-seat">+</span>
                坐下
            </div>
        </div>

        <div class="game-tips">
            桌子坐满后，自动开奖，用户离开不影响开奖<br>可以在区块链浏览器中查看每次开奖结果: <a target="_blank" href="https://eosflare.io/account/theluckygame">theluckygame</a>
        </div>
    </div>

    <div class="result" v-if="gameResult">
        <div v-if="gameResult.isWin" class="winner-img">
            <img src="../../static/img/winner.png" alt="">
        </div>
        <h2 v-html="'获胜者：<br>' + gameResult.winner.join('<br>')"></h2>
        <h3 v-html="'奖金：' + gameResult.reward"></h3>
        <table>
            <tr>
                <th>玩家</th>
                <th>点数</th>
            </tr>
            <tr v-for="item in gameResult.pointList">
                <td>{{item.account}}</td>
                <td>{{item.point}}</td>
            </tr>
        </table>
        <a href="javascript:;" class="btn-common" @click="close">确定</a>
        <p class="tips">提示：你可以在区块链浏览器中查看每次结果</p>
    </div>
  </div>
</template>

<script>
import ScatterJS from "scatter-js/dist/scatter.esm";
import Eos from "eosjs";
import _ from 'lodash';
import Vue from 'vue';

const ROOM_ID_MAP = {
    '0.1': {ids: ['21', '22', '23', '24', '25'], count: 2},
    1: {ids: ['21', '22', '23', '24', '25'], count: 2},
    10: {ids: ['41', '42', '43', '44'], count: 4},
    100: {ids: ['91', '92'], count: 8}
}

const TABLE_MAP = {
    2: {chip: '1.0000', count: 2, roomType: '1'},
    4: {chip: '10.0000', count: 4, roomType: '10'},
    9: {chip: '100.0000', count: 8, roomType: '100'}
}

const MAIN_CONTRACT = 'theluckygame';

const network = {
  blockchain: "eos",
  protocol: "https",
  host: "api1.eosasia.one",
  port: 443,
  chainId: "aca376f206b8fc25a6ed44dbdc66547c36c6c33e3a119ffbeaef943642f0e906"
};

const requiredFields = { accounts: [network] };

export default {
    name: "GameHall",
    data() {
        return {
            eosClient: null,
            currentAccount: null,
            currentPermission: null,
            scatter: null,
            roomType: null,
            tableList: [],
            tableObj: {},
            chip: null,
            // queryRoomIds: [],
            roomType: 1,
            symbol: 'EOS',
            tableUserCount: 2,
            tableId: null,
            hasShowYourResult: false,
            alreadyIn: false,
            tableIdTimer: null,
            receiptTimer: null,
            diceNumTimer: null,
            showedId: 0,
            runDice: false,
            diceNum: 1,
            userList: [],
            currentTableNum: 0,
            gameResult: null,
            balance: null,
            ref: null
            // gameResult: {
            //     winner: ['abcccccccccc'],
            //     reward: '1.9700 EOS',
            //     pointList: [{
            //         account: 'gametestguy1',
            //         point: 13
            //     }, {
            //         account: 'gametestguy3',
            //         point: 3
            //     }]
            // }
        };
    },
    created() {
        this.tableId = +this.$route.params.id;
        
        let query = location.search && location.search.substr(1)
        let queryObj = {}

        if (query) {
            _.forEach(query.split('&'), item => {
                let tempArr = item.split('=')
                queryObj[tempArr[0]] = tempArr[1]
            })
        }

        if (queryObj['ref']) {
            this.ref = queryObj['ref'];
        }
        
        this.tableUserCount = this.$route.params.id.charAt(0);
     
        this.chip = TABLE_MAP[this.tableUserCount] && TABLE_MAP[this.tableUserCount].chip;
        this.roomType = TABLE_MAP[this.tableUserCount].roomType;

        let chainId = network.chainId;
        let httpEndpoint = network.protocol + '://' + network.host + ':' + network.port;
      
        this.eosClient = Eos({httpEndpoint, chainId});

        ScatterJS.scatter.connect("localhost").then(connected => {
            if (!connected) return false;

            const scatter = ScatterJS.scatter;
            this.scatter = scatter;

            this.login();

        });

        this.getRoomInfo();
        this.tableIdTimer = setInterval(this.getRoomInfo, 5000);

        this.getReceipt();
        this.receiptTimer = setInterval(this.getReceipt, 5000);

        this.diceNumTimer = setInterval(()=> {
            if (this.diceNum < 4) {
                this.diceNum++;
            }
            else {
                this.diceNum = 1
            }
        }, 100)
    },
    methods: {
        getRoomInfo() {
            if (!this.eosClient) {
                return;
            }
            this.eosClient.getTableRows(true, MAIN_CONTRACT, this.tableId, 'plgames').then(res => {
                let tempArr = [];
                let alreadyIn = false;
                    
                if (res.rows.length) {
                    this.currentTableNum = res.rows.length;

                    this.chip = parseFloat(res.rows[0].bet).toFixed(4);

                    if (res.rows.length === this.tableUserCount) {
                        this.runDice = true;
                    }
                    _.forEach(res.rows, subItem => {
                        if (this.currentAccount && subItem.name === this.currentAccount) {
                            alreadyIn = true;
                        } else {
                            // if (parseFloat(subItem.bet) === this.roomType) {
                            tempArr.push(subItem.name);
                            // }
                        }
                        

                        
                    })
                }
                this.alreadyIn = alreadyIn;
                tempArr.length = this.tableUserCount - 1;
              
                this.userList.splice(0, this.tableUserCount, ...tempArr);
            });
        },
        login() {
            if (!this.scatter) {
                return;
            }
            else {
                if (this.scatter.identity && this.scatter.identity.accounts ) {
                    const account = scatter.identity.accounts.find(
                        x => x.blockchain === "eos"
                    );

                    this.currentAccount = account.name;
                    this.currentPermission = account.authority;
                    this.getBalance();

                    const eosOptions = { expireInSeconds: 60 };

                    this.eosClient = scatter.eos(network, Eos, eosOptions);
                    window.eos = this.eosClient;
                }
                else {
                    scatter.getIdentity(requiredFields).then(() => {
                        const account = scatter.identity.accounts.find(
                            x => x.blockchain === "eos"
                        );
                        this.currentAccount = account.name;
                        this.currentPermission = account.authority;
                        this.getBalance();

                        const eosOptions = { expireInSeconds: 60 };

                        this.eosClient = scatter.eos(network, Eos, eosOptions);
                        window.eos = this.eosClient;
                        
                    })
                    .catch(error => {
                        alert(JSON.stringify(error))
                    });
                }
                this.getRoomInfo();
            }
        },
        logout() {
            if (this.scatter) {
                this.scatter.forgetIdentity();
                this.currentAccount = null;
            }

            this.getRoomInfo();
            
        },
        getBalance() {
            if (this.currentAccount && this.eosClient) {
                this.eosClient.getCurrencyBalance('eosio.token', this.currentAccount, 'EOS').then(res => {
                    this.balance = res[0];
                })
            }
        },
        join() {
            if (!this.eosClient) {
                return;
            }

            if (!this.currentAccount) {
                this.login();
                return;
            }

            let memo = this.tableId;

            if (this.ref) {
                memo += '-' + this.ref; 
            }

            this.eosClient.transfer(this.currentAccount, MAIN_CONTRACT, this.chip + ' ' + this.symbol, memo)
                .then(res => {
                    // console.log(res);
                    this.alreadyIn = true;
                    // console.log('当前用户数量' + this.currentTableNum)
                    // console.log('房间人数' + this.tableUserCount);
                    if (++this.currentTableNum >= this.tableUserCount) {
                        this.runDice = true;
                    }
                    this.getRoomInfo();
                    this.getBalance();
                })
                .catch(err => {
                    // console.log(err);
                    // JSON 错误
                    if (_.isObject(err)) {
                        if (err.message) {
                            alert(err.message);
                        }
                        else {
                            alert(JSON.stringify(err));
                        }
                    }
                    else {
                        alert(JSON.stringify(err));
                    }
                })
        },
        back() {
            this.$router.push('/game/' + this.roomType);
        },
        getReceipt() {
            if (this.eosClient && this.currentAccount) {
                this.eosClient.getActions(this.currentAccount, -1, -1).then(res => {
                    let act = res.actions[0].action_trace.act;

                    let receiptTime = new Date(res.actions[0].block_time+'Z').getTime();
                    let now = new Date().getTime();
                 
                    // 最新的receipt, 是该合约且是当前桌号, 20秒钟以内的
                    if (act.account === MAIN_CONTRACT && act.data.result && act.data.result.room === this.tableId && now < (receiptTime + 10000)) {
                      
                        if (act.data.result.game_id === this.showedId) {
                            return;
                        }
                        this.runDice = true;
                        setTimeout(() => {
                            this.showResult(act.data.result);
                        }, 1000);
                        
                    }
                })
              
            }
        },
        showResult(result) {
            if (result.game_id !== this.showedId) {
                this.currentTableNum = 0;

                let winnerArr = _.map(result.winner, winner => winner.name);
                let resultTable = _.map(result.trades, trade => {
                    return {
                        account: trade.player,
                        point: trade.random_roll
                    }
                });
             
                let isWin = _.includes(winnerArr, this.currentAccount);

                
                this.gameResult = {
                    winner: winnerArr,
                    reward: result.payout,
                    pointList: resultTable,
                    isWin: isWin
                }
                this.showedId = result.game_id;
                this.runDice = false;
                
            }
        },
        close() {
            this.gameResult = null;
        }
    },
    beforeDestroy() {
        clearInterval(this.tableIdTimer);
        clearInterval(this.receiptTimer);
        clearInterval(this.diceNumTimer);
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">

.bg-table{
  background: url("../../static/img/bg-table.png") no-repeat center center;
  background-size: cover;
  position: fixed;
  min-height: 600px;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.the-table {
    background: url("../../static/img/table.png") no-repeat bottom center;
    background-size: 100% 30%;
    position: fixed;
    min-height: 600px;
    left: 0;
    right: 0;
    bottom: 45px;
    width: 100%;
    height: 100%;
    z-index: 1;
}

.content {
  position: absolute;
  margin: auto;
  z-index: 99;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.table-title {
    position: relative;
    top: 15px;
    font-size: 14px;
    color: #fff;
    font-weight: bold;
    line-height: 2;
    text-align: center;
    background: url('../../static/img/bg-tips.png') no-repeat center center;
    background-size: contain;
}
.waiting {
    font-size: 13px;
    color: #333;
}

.run-dice {
    position: absolute;
    bottom: 170px;
    display: block;
    left: 0;
    right: 0;
    text-align: center;
    
    img {
        width: 60px;
        height: 60px;
    }
}



.btn-type {
  display: block;
  margin: 20px auto;
  width: 360px;
  height: 130px;
  background-size: 360px 130px;
  background-repeat: no-repeat;
}

.one-eos {
  background-image: url("../../static/img/btn-1eos.png");
}

.ten-eos {
  background-image: url("../../static/img/btn-10eos.png");
}

.hundred-eos {
  background-image: url("../../static/img/btn-100eos.png");
}

.game-table {
    position: relative;
    display: inline-block;
    background-repeat: no-repeat;
    background-size: 100% 100%; 

    h3 {
        position: absolute;
    }
}
.empty-seat {
    position: relative;
    cursor: pointer;
    width: 60px;
    height: 60px;
    border-radius: 4px;
    background-color: #966644;
    border: 4px solid #e5a067;
    box-shadow: inset 0px 0px 3px 0px #333;
    // background: url('../../static/img/chair.png') no-repeat;
    // background-size: 100% 100%; 
    display: block;
    color: #edddc1;
    font-family: serif;
    font-size: 48px;
    line-height: 60px;
    margin: 0 auto;
}

.my-seat {
    position: absolute;
    bottom: 65px;
    text-align: center;
    display: block;
    left: 0;
    right: 0;

    .empty-seat {
        box-shadow: 0px 1px 5px 0 #333;
    }
}

.game-tips {
    font-size: 12px;
    padding: 6px 0;
    color: #f1f1f1;
    background-color: #333;
    position: absolute;
    bottom: 0;
    text-align: center;
    display: block;
    left: 0;
    right: 0;

    a {
        color: #fff;
    }
}

.user-list-3 {
    .user {
        width: 30%;
    }
}

.user-list-8 {
    .user {
        width: 24%;
        margin-bottom: 10px;
    }
}

.user {
    vertical-align: middle;
}

.user-seat {
    // position: absolute;
    display: inline-block;
    font-weight: bold;
    color: #333;
    font-size: 12px;
    text-align: center;
}

.user-list {
    position: absolute;
    bottom: 45%;
    width: 100%;
    text-align: center;
}
.user {
    display: inline-block;
}

.avatar {
    width: 60px;
    height: 60px;
    display: block;
    margin: 0 auto 4px;
    border: 4px solid #e5a067;
    border-radius: 4px;
}

.result {
    position: fixed;
    text-align: center;
    z-index: 999;
    width: 90%;
    top: 110px;
    max-width: 400px;
    left: 0;
    right: 0;
    margin: 0 auto;
    background: #fffaef;
    border: 10px solid #e5a067;
    border-radius: 10px;
    padding: 0 10px 10px; 
    box-sizing: border-box;

    .winner-img {
        position: absolute;
        top: -40px;
        left: 50%;
        margin-left: -25px;

        img {
            width: 50px;
        }
    }

    h2 {
        font-size: 18px;
        color: #e27e00;
    }

    h3 {
        font-size: 16px;
        color: #e27e00;
        margin-bottom: 30px;
    }

    table {
        width: 100%;
        color: #333;
        margin-bottom: 20px;

        td {
            width: 50%;
        }
    }
}

.tips {
    font-size: 12px;
    color: #999;
}


@media screen and (max-width: 480px) {
  
}
</style>



