<template>
  <div class="game-hall" >
    <div class="bg-game-hall"></div>
    <div class="content">
        <div class="opt-bar">
            <div @click="back" class="back"></div>
            <div class="float-right">
                <span @click="openInvite">邀请</span>
                <div v-if="currentAccount" class="user-info"><span class="user-name">{{currentAccount}}</span><a href="javascript:;" class="btn-common exit" @click="logout">退出</a></div>
                
                <a href="javascript:;" class="btn-common " v-else @click="login">登录</a>
            </div>
            
        </div>

        <div class="invite-input" v-if="inviteOpened">
            <p>邀请好友参加游戏，获得好友投注的0.5%</p>
            <i @click="closeInvite">x</i>
            <input type="text" id="inviteurl" :value="inviteUrl">
            <button class="btn-common" data-clipboard-action="copy" data-clipboard-target="#inviteurl" id="copy_btn" >复制</button>
        </div>
        
        <div :class="['table-' + tableUserCount, 'table-wrap']">
            <div class="game-table" v-for="item in tableList">
                <h3 @click="enterRoom(item.id)">桌号：{{item.id}} <br> <span class="price"> {{item.chip}}{{symbol}} /位 </span> </h3>
                <p v-for="(user, index) in item.users">
                    <span :class="['seat-' + index, 'user-seat']" v-if="user">
                        <img class="avatar" src="../../static/img/avatar.png" alt="">
                        {{user}}</span> 
                    <span @click="join(item.id)" :class="['seat-' + index, 'empty-seat']" v-else></span>
                </p>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import ScatterJS from "scatter-js/dist/scatter.esm";
import Eos from "eosjs";
import _ from 'lodash';
import Vue from 'vue';
import ClipboardJS from 'clipboard';

const ROOM_ID_MAP = {
    1: {ids: ['21', '22', '23', '24', '25'], count: 2},
    10: {ids: ['41', '42', '43', '44'], count: 4},
    100: {ids: ['91', '92'], count: 9}
}

const MAIN_CONTRACT = 'theluckygame';

const network = {
  blockchain: "eos",
  protocol: "https",
  host: "api1.eosasia.one",
  port: 443,
  chainId: "aca376f206b8fc25a6ed44dbdc66547c36c6c33e3a119ffbeaef943642f0e906"
};

const requiredFields = { accounts: [network] };

export default {
    name: "GameHall",
    data() {
        return {
            eosClient: null,
            currentAccount: null,
            currentPermission: null,
            scatter: null,
            roomType: null,
            tableList: [],
            tableObj: {},
            chip: null,
            queryRoomIds: [],
            symbol: 'EOS',
            inviteOpened: false,
            tableUserCount: 2,
            clipboard: null
        };
    },
    computed: {
        inviteUrl: vm => {
            return 'https://eos-dice.github.io/?ref=' + (vm.currentAccount || '你的账号');
        }
    },

    created() {
        this.roomType = +this.$route.params.type;
        // test
        // this.chip = '0.1000';
        this.chip = parseFloat(this.roomType).toFixed(4);
        this.tableUserCount = (ROOM_ID_MAP[this.roomType] && ROOM_ID_MAP[this.roomType].count) || 2;
        this.queryRoomIds = (ROOM_ID_MAP[this.roomType] && ROOM_ID_MAP[this.roomType].ids) || [];
        this.tableList = _.map(this.queryRoomIds, id => {
            let tempArr = [];
            tempArr.length = this.tableUserCount;
            return {id: id, users: tempArr, chip: this.chip};
        });

        let chainId = network.chainId;
        let httpEndpoint = network.protocol + '://' + network.host + ':' + network.port;
      
        this.eosClient = Eos({httpEndpoint, chainId});

        ScatterJS.scatter.connect("localhost").then(connected => {
            if (!connected) return false;

            const scatter = ScatterJS.scatter;
            this.scatter = scatter;

            this.login();

            
        });

        this.getRoomInfo();
        this.tableIdTimer = setInterval(this.getRoomInfo, 10000);
    },
    methods: {
        getRoomInfo() {
            if (!this.eosClient) {
                return;
            }
            _.forEach(this.queryRoomIds, id => {
                // id 为桌号
                this.eosClient.getTableRows(true, MAIN_CONTRACT, id, 'plgames').then(res => {
                    let index = 0;
                    _.forEach(this.tableList, (item, key) => {
                        if (item.id === id) {
                            index = key;
                        }
                    });

                    // 这个房间的user 数组
                    let tempArr = [];
                    let tableChip = this.chip;
                    
                    if (res.rows.length) {
                        tableChip = parseFloat(res.rows[0].bet).toFixed(4);
                        _.forEach(res.rows, subItem => {
                            tempArr.push(subItem.name);
                        })
                        
                    }
                    tempArr.length = this.tableUserCount;
                    this.tableList.splice(index, 1, {id: id, users: tempArr, chip: tableChip})
                })
            });
        },
        login() {
            if (!this.scatter) {
                return;
            }
            else {
                if (this.scatter.identity && this.scatter.identity.accounts ) {
                    const account = scatter.identity.accounts.find(
                        x => x.blockchain === "eos"
                    );

                    this.currentAccount = account.name;
                    this.currentPermission = account.authority;

                    const eosOptions = { expireInSeconds: 60 };

                    this.eosClient = scatter.eos(network, Eos, eosOptions);
                    window.eos = this.eosClient;
                }
                else {
                    scatter.getIdentity(requiredFields).then(() => {
                        const account = scatter.identity.accounts.find(
                            x => x.blockchain === "eos"
                        );
                        this.currentAccount = account.name;
                        this.currentPermission = account.authority;

                        const eosOptions = { expireInSeconds: 60 };

                        this.eosClient = scatter.eos(network, Eos, eosOptions);
                        window.eos = this.eosClient;
                        
                    })
                    .catch(error => {
                        console.error(error);
                    });
                }
                
            }
        },
        enterRoom(id) {
            this.$router.push('/table/' + id);
        },
        logout() {
            if (this.scatter) {
                this.scatter.forgetIdentity();
                this.currentAccount = null;
            }
        },
        join(id) {
            this.$router.push('/table/' + id);
            // if (!this.eosClient || !this.currentAccount) {
            //     return;
            // }
            // this.eosClient.transfer(this.currentAccount, MAIN_CONTRACT, this.chip + ' ' + this.symbol, id).then(res => {
            //     this.$router.push('/table/' + id);
            // });
        },
        openInvite(){
            this.inviteOpened = true;
            this.clipboard = new ClipboardJS('#copy_btn');

            this.clipboard.on('success', e => {
                alert("复制成功");
                e.clearSelection();
                this.inviteOpened = false;
            });  
            this.clipboard.on('error', e => {
                alert('复制失败，请长按复制');
            });
           
        },
        closeInvite() {
            this.inviteOpened = false;
        },
        back() {
            this.$router.push('/');
        }
    },
    beforeDestroy() {
        clearInterval(this.tableIdTimer);
        this.clipboard && this.clipboard.destroy();
    }
};
</script>

